<template>
    <div id="login" v-if="heightReset" :style="`height:${clientHeight}px; overflow:scrool`">
        <el-card v-if="loginType===1" class="box-card login-card" >
            <div style="line-height:30px; margin-bottom:20px; text-align: center;"></div>
            <el-form  :model="loginInfo" label-width="100px" ref="loginInfo" :rules="loginRules">
              <el-form-item label=":" prop="account">
                <el-input class="input" v-model="loginInfo.account" placeholder=""></el-input>
              </el-form-item>
                 <el-form-item label=":" prop="password">
                <el-input type="password"   class="input" v-model="loginInfo.password" @keyup.enter.native="loginIn" placeholder=""></el-input>
              </el-form-item>
               <el-button class="button" type="primary" round @click="loginIn"></el-button>
               <el-button  type="text"  @click="toRegister()" ></el-button>
                <el-button  type="text"  @click="loginType=3" ></el-button>
            </el-form>
        </el-card>
        <el-card v-else-if="loginType===2" class="box-card login-card" >
            <div style="line-height:30px; margin-bottom:20px; text-align: center;"></div>
            <el-form  :model="registerInfo" label-width="110px" ref="registerInfo" :rules="registerRules">
                <el-form-item label=":" prop="name">
                <el-input class="input" v-model="registerInfo.name" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label=":" prop="account">
                <el-input class="input" v-model="registerInfo.account" placeholder=""></el-input>
              </el-form-item>
                <el-form-item label=":" prop="password">
                <el-input   class="input" type="password" v-model="registerInfo.password" placeholder=""></el-input>
              </el-form-item>
                <el-form-item label=":" prop="check_password">
                <el-input type="password"   class="input" v-model="registerInfo.check_password" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label=":"  prop="email_addr">
                <el-input   class="input" v-model="registerInfo.email_addr" placeholder=""></el-input>
              </el-form-item>
                <el-form-item label=":"  prop="code">
                <el-input @keyup.enter.native="register"   class="auth_code" style="width:60%" v-model="registerInfo.code" placeholder=""></el-input>
                <el-button :disabled="disabled" style="float:right;height:35px;width: 120px;" @click="getAuthCode">{{butName}}</el-button>
              </el-form-item>

               <el-button class="button" type="primary" round @click="register"></el-button>
               <el-button  type="text" @click="toLogin()" ></el-button>
            </el-form>
        </el-card>

        <el-card v-else class="box-card login-card" >
            <my-pwd :pwdType=2 :loginType.sync="loginType" />
        </el-card>


    </div>
    
</template>

<script>
    import {getClientHeight, emailVerify} from '../../utils/common'
    import {setCaCheSession, setCaCheAcc, setCaCheName, setCaCheRoleType, setCaCheEmail} from '../../utils/localStorage'
    import service from "../../utils/request";
    import {Message} from 'element-ui';
    import myPwd from '../common/person'

    export default {
        name: "login",
        components:{myPwd},
        data() {
            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else {
                    if (this.registerInfo.check_password !== '') {
                        this.$refs.registerInfo.validateField('checkPass');
                    }
                    callback();
                }
            };
            var validatePass2 = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if (value !== this.registerInfo.password) {
                    callback(new Error('!'));
                } else {
                    callback();
                }
            };
            return {
                loginType:1, //1,2,3
                clientHeight:'',
                heightReset:true,
                loginInfoCopy:{
                    account:'',
                    password:'',
                    type:1,  //2,1

                },
                loginInfo:{},
                isLogin:true,
                registerInfoCopy:{
                    name:'',
                    account:'',
                    password:'',
                    check_password:'',
                    email_addr:'',
                    code:'',
                    type:1
                },
                registerInfo:{},
                loginRules: {
                     account: [
                         {required: true, message: '', trigger: 'blur'},
                     ],
                     password: [
                         {required: true, message: '', trigger: 'blur'},
                     ],
                 },
                registerRules: {
                     account: [
                         {required: true, message: '', trigger: 'blur'},
                     ],
                     name: [
                         {required: true, message: '', trigger: 'blur'},
                     ],
                    password: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: validatePass, trigger: 'blur'}
                    ],
                    check_password: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: validatePass2, trigger: 'blur' }
                     ],
                    email_addr: [
                         {required: true, message: '', trigger: 'blur'},
                     ],

                 },
                timerout:null,
                disabled:false,
                butName:''
            }
        },
        methods: {
            loginIn(){
                this.loginInfo.type = emailVerify(this.loginInfo.account) ? 2 : 1
                this.$refs.loginInfo.validate(async valid => {
                    if (!valid) return;
                    service.request({
                        method: "post",
                        url: "/login",
                        data: this.loginInfo
                    }).then(res => {
                        setCaCheSession(res.data.session)
                        setCaCheAcc(res.data.account)
                        setCaCheEmail(res.data.email_addr)
                        setCaCheName(res.data.name)
                        setCaCheRoleType(res.data.roleType)
                        this.$store.commit("setUserName", res.data.name);
                        this.$store.commit("setRoleType", res.data.roleType);
                        this.$store.commit("setUserNameIsChange", true)
                        if (res.data.roleType === '1') {
                            this.$router.push('/paper')
                        } else {
                            this.$router.push('/manage')
                        }
                    })
                })
            },
            getAuthCode(){
                this.$refs.registerInfo.validate(async valid => {
                    if (!valid) return;
                    if (emailVerify(this.registerInfo.email_addr)) {
                        service.request({
                            method: "post",
                            url: "/verification",
                            data: this.registerInfo
                        }).then(res => {
                            this.setInterval()
                            Message.success('');
                        })
                    } else {
                        Message.warning('');
                    }
                })
            },
            toRegister(){
                this.loginType = 2
                this.registerInfo = {...this.registerInfoCopy}
                this.$nextTick(() => {
                    this.$refs.registerInfo.resetFields();
                });

            },
            toLogin() {
                this.disabled = false
                this.loginType = 1
                this.loginInfo = {...this.loginInfoCopy}
                this.$nextTick(() => {
                    this.$refs.loginInfo.resetFields();
                });

            },
            register(){
                this.$refs.registerInfo.validate(async valid => {
                    if (!valid) return;
                    if(this.registerInfo.code === ''){
                        Message.warning("");
                    }else if(this.registerInfo.name === ''){
                        Message.warning("");
                    }else  {
                        service.request({
                            method: "post",
                            url: "/user",
                            data: this.registerInfo
                        }).then(res => {
                            Message.success('');
                            this.loginType = 1
                            clearTimeout(this.timerout)
                            this.timerout = null
                            this.disabled = false
                        })
                    }
                })
            },
            //
            setInterval() {
                if (this.timerout == null) {
                    this.disabled = true
                    let count = 30
                    this.timerout = setInterval(() => {
                        this.disabled = true
                        count = count - 1
                        this.butName = count + 's'
                        if (count === 0) {
                            clearTimeout(this.timerout)
                            this.timerout = null
                            this.butName = ''
                            this.disabled = false
                        }
                    }, 1000)
                }
            },

        },
        mounted() {
            this.clientHeight = getClientHeight();
        }
    }
</script>

<style>
    #login{
        background:url('../../assets/img/login_back.jpg');
        width:100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .login-card{
        width:450px;
    }

    .input .el-input__inner{
        height:35px;
        width:300px
    }

    .auth_code .el-input__inner{
        height:35px;
        width:170px;
        float:left
    }


    .button{
        line-height: 10px;
        width: 400px;
    }

</style>